import React, { useState, useEffect } from 'react'; // Import useState hook
import Nav from "../navigation/Nav";
import Footer from "../footer/Footer";
import "./home.css";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { Navigation, Autoplay } from 'swiper/modules';
import Doctors from '../Doctors/Doctors';

const Hstyle = {
  color: "#ffffffb9",
  textAlign: "center",
  marginRight: "30px",
};

function Home() {
  // State to store the email input value
  const [email, setEmail] = useState('');

  // Handle input changes
  const handleChange = (event) => {
    setEmail(event.target.value);
  };

  // Handle form submission
  const handleSubmit = (event) => {
    event.preventDefault(); // Prevents the default form submission
    alert('Email submitted: ' + email);

    // You can add your email subscription logic here, like an API call.
    // After submission, you may want to clear the email input
    setEmail('');
  };
  useEffect(() => {
    AOS.init();
  }, []);
  
  return <>
    <Nav />
    <main>
      <Swiper
        className="mySwiper"
        navigation={true}
        modules={[Navigation, Autoplay]}
        loop={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
      >
        <SwiperSlide className="slide1">
          <div className="placement">
            <div className="slogan">
              <h1 className="animate__animated animate__backInUp">
                Soins Adquat pour Tous.
              </h1>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide className="slide2">
          <div className="placement">
            <div className="slogan">
              <h1 className="animate__animated animate__backInUp">
                Soins d'excellence, bien-tre au cur
              </h1>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide className="slide3">
          <div className="placement">
            <div className="slogan">
              <h1 className="animate__animated animate__backInUp">
                Une quipe proche de vous,  chaque tape
              </h1>
            </div>
          </div>
        </SwiperSlide>
      </Swiper>
    </main>

    <section className="about">
      <div className="section-title">
        <h2>PourQuoi Nous ?</h2>
      </div>

      <div className="about-container">
        <div className="about-box">
          <div
            className="about-img"
            data-aos="fade-left"
            data-aos-offset="300"
            data-aos-duration="1200"
            data-aos-easing="ease-in-sine"
          >
            <img src={require('../Images/image6.jpg')} alt="Luxury" />
          </div>
          <div
            className="about-text"
            data-aos="fade-right"
            data-aos-offset="300"
            data-aos-duration="1200"
            data-aos-easing="ease-in-sine"
          >
<h3>Des Experts Dvous  Votre Vie</h3>
<p>
  Chaque intervention est plus qu'une mission : c'est un engagement. Notre quipe de chirurgiens expriments veille sur vous avec prcision, humanit et un dvouement sans faille. Votre vie est entre de bonnes mains.
</p>
          </div>
        </div>

        <div className="about-box">
          <div
            className="about-text"
            data-aos="fade-left"
            data-aos-offset="300"
            data-aos-duration="1200"
            data-aos-easing="ease-in-sine"
          >
<h3>Une quipe de Chirurgiens d'Excellence</h3>
<p>
  Votre sant est notre priorit absolue. Avec des chirurgiens hautement qualifis et expriments, vous tes entre des mains expertes pour chaque tape de votre parcours mdical.
</p>

          </div>
          <div
            className="about-img"
            data-aos="fade-right"
            data-aos-offset="300"
            data-aos-duration="1200"
            data-aos-easing="ease-in-sine"
          >
            <img src={require('../Images/image7.jpg')} alt="Advanced" />
          </div>
        </div>

        <div className="about-box">
          <div
            className="about-img"
            data-aos="fade-left"
            data-aos-offset="300"
            data-aos-duration="1300"
            data-aos-easing="ease-in-sine"
          >
            <img src={require('../Images/image8.jpg')} alt="Resort" />
          </div>
          <div
            className="about-text"
            data-aos="fade-right"
            data-aos-offset="300"
            data-aos-duration="1500"
            data-aos-easing="ease-in-sine"
          >
          <h3>Soins Personnaliss</h3>
<p>
  Que ce soit pour une consultation ou un traitement rapide, nous mettons tout en uvre pour faire de votre exprience un moment rassurant et bienveillant.
</p>
          </div>
        </div>
      </div>
    </section>



    <Doctors />



    <div className="container">
      <div className="left">
        <h1 id='appointment'>Book an Appointment</h1>
        <div className="service-box">
          <div className="service-icon"></div>
          <div className="service-text">General Consultation</div>
        </div>
        <div className="service-box">
          <div className="service-icon"></div>
          <div className="service-text">Vaccination</div>
        </div>
        <div className="service-box">
          <div className="service-icon"></div>
          <div className="service-text">Minor Surgery</div>
        </div>
      </div>
      <div className="right">
        <form>
          <div className="form-group">
            <label htmlFor="name">Name</label>
            <input type="text" id="name" name="name" required />
          </div>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input type="email" id="email" name="email" required />
          </div>
          <div className="form-group">
            <label htmlFor="service">Select Service</label>
            <select id="service" name="service" required>
              <option value="consultation">General Consultation</option>
              <option value="vaccination">Vaccination</option>
              <option value="surgery">Minor Surgery</option>
            </select>
          </div>
          <div className="form-group">
            <label htmlFor="message">Message</label>
            <textarea id="message" name="message" rows="4"></textarea>
          </div>
          <input type="submit" value="Book Appointment" />
        </form>
      </div>
    </div>


    <div className="newsletter">
      <div className="container_letter">
        <h1 id="HeadText" className="animate__animated animate__backInUp">
          Subscribe to our newsletter
        </h1>
        <h3 style={Hstyle}>Subscribe to our NewsLetter and stay updated.</h3>
        <form onSubmit={handleSubmit}>
          <input
            type="email"
            placeholder="Enter Your Email"
            value={email} // Bind input value to state
            onChange={handleChange} // Update state on input change
            required
          />
          <button type="submit">Subscribe</button>
        </form>
      </div>
    </div>
   
    <Footer />
  </>;
}

export default Home;
